.box div{
    position: absolute;
    height: 50px;
    width: 50px;
    border: 3px solid white;
    border-radius: 8px;
}

.box div:first-child{
    top: 20%;
    left: 32%;
    animation: animatebox 2s linear infinite;
}

.box div:nth-child(2){
    top: 10%;
    left: 22%;
    animation: animatebox 5s linear infinite;
}

.box div:nth-child(3){
    bottom: 10%;
    left: 22%;
    animation: animatebox 4s linear infinite;
}

.box div:nth-child(4){
    bottom: 40%;
    right: 22%;
    animation: animatebox 7s linear infinite;
}

.box div:nth-child(5){
    bottom: 40%;
    left: 10%;
    animation: animatebox 4s linear infinite;
}


.box div:nth-child(6){
    top: 20%;
    right: 32%;
    animation: animatebox 3s linear infinite;
}

.box div:nth-child(7){
    top: 10%;
    right: 22%;
    animation: animatebox 8s linear infinite;
}

.box div:nth-child(8){
    bottom: 10%;
    right: 22%;
    animation: animatebox 2s linear infinite;
}

@keyframes animatebox{
    0%{
        opacity: 1;
        transform: translateY(0) scale(0) rotate(0);
    }

    100%{
        opacity: 0;
        transform: translateY(-80px) scale(1.5) rotate(90deg);
    }
}