.weatherLogo {
    width: 140px;
    height: 140px;
    margin: 40px auto;
}

.chooseCityLabel {
    color: black;
    font-size: 18px;
    font-weight: bold;
    margin: 10px auto;
}

.searchBox {
    display: flex;
    flex-direction: row;
    border-radius: 2px;
    color: black;
    font-weight: bold;
    margin: 20px auto;
}

.searchBox input {
    padding: 10px;
    font-size: 14px;
    border: black solid 2px;
    border-right: none;
    outline: none;
    font-weight: bold;
}

.searchBox button {
    background-color: rgb(56, 54, 54);
    color: #eee;
    padding: 10px;
    border: 1px solid rgb(56, 54, 54);
    font-size: 14px;
    outline: none;
    font-weight: bold;
    cursor: pointer;
}

@media (max-width: 1150px ){
    .searchBox{
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        border: none;
    }

    .searchBox input {
        margin: 10px 0px;
        border: black solid 2px;
    }
}